import React from "react"

import { graphql } from "gatsby"

import { Link } from "react-scroll"

import Select from 'react-select';
import DatePicker from "react-datepicker";
import { registerLocale } from  "react-datepicker";
import { addDays } from 'date-fns';
import {uk} from 'date-fns/esm/locale'
import "react-datepicker/dist/react-datepicker.css";

import { TelegramClient } from 'messaging-api-telegram';

import logo from "../images/logo.svg"

import Layout from "../components/layout"
import SEO from "../components/seo"

registerLocale('uk', uk);

const options = [
  { value: '   ', label: '   ' },
  { value: '    ', label: '    ' },
];

const client = TelegramClient.connect('805786025:AAEiG8lF4TTTfwswDJu5kIkhXTo7wBpxPog');

class IndexPage extends React.Component {
  constructor(props) {
    super(props);

    this.handleName = this.handleName.bind(this);
    this.handlePhone = this.handlePhone.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.state = {
    	name: '',
    	phone: '',
		startFromDate: null,
		startToDate: null,
		selectedOption: null,
    }
  }

  handleStartDate = date => {
    this.setState({
      startFromDate: date
    });
  };

  handleEndDate = date => {
    this.setState({
      startToDate: date
    });
  };

  handleSelectChange = selectedOption => {
    this.setState(
      { selectedOption: selectedOption }
    );
  };

  handleName(e) {
  	this.setState({name: e.target.value})
  }

  handlePhone(e) {
  	this.setState({phone: e.target.value})
  }

  handleSubmit(e) {
    e.preventDefault();

	var messageText = "<b> </b> \n", elements = e.target.elements;

	if(this.state.name && this.state.phone && this.state.selectedOption && this.state.startToDate && this.state.startFromDate) {
		messageText += '<b>\':</b> ' + this.state.name + ' \n';
		messageText += '<b>:</b> ' + this.state.phone + ' \n';
		messageText += '<b>:</b> ' + elements.service.value + ' \n';
		messageText += '<b>:</b> ' + elements.date_from.value + ' \n';
		messageText += '<b>:</b> ' + elements.date_to.value

		client.sendMessage('-349106194', messageText, {
			parse_mode: 'HTML',
			disable_web_page_preview: true,
		});

		alert(',   !    \'  .');

		this.setState({
	    	name: '',
	    	phone: '',
			startFromDate: null,
			startToDate: null,
			selectedOption: null,
		})
	} else {
		alert(',  ,  !');
	}

  }

  render() {

  	const info = this.props.data;

    return (
	  <Layout>
	    <SEO title="KTZ Company" />

		<header className="header">
			<div className="container">
				<div className="header--container">
					<Link to="first" className="static" spy={true} smooth={true} duration={500}><img src={logo} alt={info.site.siteMetadata.title} /></Link>
					<div className="header--nav">
						<input id="header--nav__toggle" type="checkbox"/>
						<label htmlFor="header--nav__toggle" className="header--nav__toggle">
							<span /><span /><span />
						</label>
						<ul>
							<li><Link activeClass="active" to="services" spy={true} smooth={true} duration={500} ></Link></li>
							<li><Link activeClass="active" to="aboutus" spy={true} smooth={true} duration={500} > </Link></li>
							<li><Link activeClass="active" to="contacts" spy={true} smooth={true} duration={500} ></Link></li>
							<li><button className="button button--full"> </button></li>
						</ul>
					</div>
				</div>
			</div>
		</header>

		<main>
			<div id="first" className="first-screen" ref={(div) => { this.first = div; }}>
				<div className="container">
					<form className="orderform" onSubmit={this.handleSubmit}>
						<h3 className="orderform--title"> </h3>
						<div className="orderform--name">
							<input type="text" name="name" placeholder="  '" onChange={this.handleName} value={this.state.name} required />
						</div>
						<div className="orderform--phone">
							<input type="tel" name="phone" pattern="^[0-9-+\s()]*$" placeholder="  " onChange={this.handlePhone} value={this.state.phone} required />
						</div>
						<div className="orderform--select">
							<Select
								name="service"
								required={true}
								placeholder=" "
								clearable={false}
								searchable={false}
								value={this.state.selectedOption}
								onChange={this.handleSelectChange}
								options={options}
							/>
						</div>
						<div className="orderform--date">
							<div className="orderform--date__from">
								<DatePicker
									name="date_from"
									autoComplete="off"
									required={true}
									calendarClassName="orderform--calendar"
									selected={this.state.startFromDate}
									selectsStart
									placeholderText=" (..)"
									onChange={this.handleStartDate}
									startDate={this.state.startFromDate}
									endDate={this.state.startToDate}
									minDate={new Date()}
									maxDate={this.state.startToDate?this.state.startToDate:null}
									dateFormat="dd.MM.yyyy"
									locale="uk"
								/>

							</div>
							<div className="orderform--date__to">
								<DatePicker
									name="date_to"
									autoComplete="off"
									required={true}
									calendarClassName="orderform--calendar"
									selected={this.state.startToDate}
									selectsEnd
									placeholderText=" (..)"
									onChange={this.handleEndDate}
									startDate={this.state.startFromDate}
									endDate={this.state.startToDate}
									minDate={this.state.startFromDate?addDays(this.state.startFromDate, 1):addDays(new Date(), 1)}
									dateFormat="dd.MM.yyyy"
									locale="uk"
								/>
							</div>
						</div>
						<div className="orderform--button">
							<input className="button button--full" type="submit" value=""/>
						</div>
					</form>
				</div>
			</div>

			<div id="services" className="services services--1">
				<div className="container">
					<div className="services--container">
						<div className="services--1-right">
							<div className="services--1-img"></div>
						</div>
						<div className="services--1-left">
							<div className="services--content">
								<div className="services--content__category"></div>
								<h2 className="services--content__title">   </h2>
								<div className="services--content__text">Lorem Ipsum -  -"",      . Lorem Ipsum , ,  ""   XVI ,             . ""     ' ,      ,    .    60-        Letraset,     Lorem Ipsum,   -    '    Aldus Pagemaker,     Lorem Ipsum.</div>
								<div className="services--content__info">
									<div className="services--content__info-price">: <span>1000 /<sup>2</sup></span></div>
									<div className="services--content__info-callback">
										<button className="button button--full"> </button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

			<div className="services services--2">
				<div className="container">
					<div className="services--container">
						<div className="services--2-left">
							<div className="services--2-img"></div>
						</div>
						<div className="services--2-right">
							<div className="services--content">
								<div className="services--content__category"></div>
								<h2 className="services--content__title">     <span>(  40 )</span></h2>
								<div className="services--content__text">Lorem Ipsum -  -"",      . Lorem Ipsum , ,  ""   XVI ,             . ""     ' ,      ,    .    60-        Letraset,     Lorem Ipsum,   -    '    Aldus Pagemaker,     Lorem Ipsum.</div>
								<div className="services--content__info">
									<div className="services--content__info-price">: <span>1000 /<sup>2</sup></span></div>
									<div className="services--content__info-callback">
										<button className="button button--full"> </button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

			<div id="aboutus" className="aboutus">
				<div className="container">
					<div className="aboutus--title" data-backtitle=" "> </div>
					<div className="aboutus--container">
						<div className="aboutus--left">
							<div className="aboutus--img"></div>
						</div>
						<div className="aboutus--right">
							<div className="aboutus--text">Lorem Ipsum -  -"",      . Lorem Ipsum , ,  ""   XVI ,             . ""     ' ,      ,    .    60-        Letraset,     Lorem Ipsum,   -    '    Aldus Pagemaker,     Lorem Ipsum.</div>
							<div className="aboutus--signature"> KTZ Company</div>
						</div>
					</div>
				</div>
			</div>
		</main>

		<footer id="contacts" className="footer">
			<div className="footer--contacts">
			  <div className="container">
			    <div className="footer--container">
			      <Link to="first" className="static" spy={true} smooth={true} duration={500}><img src={logo} alt={info.site.siteMetadata.title} /></Link>
			      <a href="tel:+380973295976" className="footer--phone">+380 97 329 5976</a>
			    </div>
			  </div>
			</div>
			<div className="footer--copyright">
			  <div className="container">
			    <div className="footer--copyright__text">
			       {new Date().getFullYear()}
			      {` `}
				  {info.site.siteMetadata.title}
			    </div>
			  </div>
			</div>
		</footer>

	  </Layout>
	)
  }
}

export default IndexPage

export const pageQuery = graphql`
  query siteTitleQueryAndSiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`